import React from "react";

export const ShowPersonDetails = (props) => {
  const personData = props.item;
  return (
    <article>
      <hr />
      <h2>(ID: {personData.PersonId}) {personData.LastName}, {personData.FirstName}</h2>
      <p><strong>City:</strong> {personData.City}</p>
      <p><strong>Country:</strong> {personData.Country}</p>
      <p><strong>Languages:</strong> {personData.Languages}</p>
    </article>
  );
};

export default ShowPersonDetails;

