import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import ShowPersonDetails from "./ShowPersonDetails";

const PeopleTableBody = (props) => {
  const [personData, setPerson] = useState("");

  const GetDetails = async (id) => {
    const URL = "/api/react/" + id;
    await fetch(URL, {
      headers: {
        accepts: "application/json",
      },
    })
    .then((response) => response.json())
    .then((data) => setPerson(data));
  }

  const ShowDetails = (id) => {
    useEffect(() => {
      GetDetails(id);
    }, [personData]);
    if (!personData) return;
    ReactDOM.render(<ShowPersonDetails item={personData} />, document.getElementById('Details'));
  }

  const rows = props.peopleData.map((row, index) => {
    return (
      <tr key={index}>
        <td>{row.PersonId}</td>
        <td>{row.FirstName}</td>
        <td>{row.LastName}</td>
        <td><button className="details" value={row.PersonId} onClick={e => ShowDetails(e.target.value)}>Details</button></td>
      </tr>
    );
  });
  return (
    <tbody>
      {rows}
    </tbody>
  );
};

export default PeopleTableBody;

