import React from "react";
import { ShowPerson } from "./ShowPerson";
import { PeopleSection } from "./StyledComponents";

export const PeopleDisplay = (props) => {
    const showPeople = () => {
        return props.people.map((person) => <ShowPerson key={person.PersonId} item={person} />);
    };
    return <PeopleSection>{showPeople()}</PeopleSection>;
};

